
export default {
  name: "Toolbar",
  props: {
    cropStatus: {
      type: Object,
      default: () => {
        return {
          cropping: false,
          dragMode: "move",
          previewUrl: null,
        };
      },
    },
  },
  emits: [
    "delete",
    "clear",
    "preview",
    "export",
    "drag:mode",
    "zoom",
    "rotate",
    "scale",
    "aspectratio:set",
    "cropbox:move",
    "cropbox:resize",
  ],
  data() {
    return {
      square: false,
      more: false,
      span: 1,
      contrast: false
    };
  },
  methods: {
    changeSpan() {
      if (this.span === 1) {
        this.span = 5;
      } else if (this.span === 5) {
        this.span = 10;
      } else {
        this.span = 1;
      }
    },
    // 调整按钮图标背景，增加对比度
    setContrast() {
      this.contrast = !this.contrast
      let bg = this.contrast ? 'rgb(245 241 241)' : '';
      document.querySelectorAll('i').forEach((i) => {
        i.style.background = bg;
      });
    }
  },
};
